<template>
    <div class="file-list-page">
        <a-spin tip="..." :spinning="spinning">
            <a-layout ref="main">
                <a-layout-sider class="file-list-menu">
                    <div class="logo">
                        <img src="../../assets/images/logo.svg" alt="" /><span
                            ></span
                        >
                    </div>
                    <a-directory-tree
                        :draggable="true"
                        :showIcon="false"
                        :tree-data="menuList"
                        :blockNode="true"
                        :selectedKeys="selectedKeys"
                        :default-expanded-keys="expandedKeys"
                        @drop="(event) => addNewItem(event)"
                        @mouseleave="leave()"
                        @click="(e) => handleSelectMenuItem(e)"
                        @dragover="arrive"
                        @dragleave="fileLeave"
                    />
                </a-layout-sider>
                <a-layout>
                    <a-layout-header>
                        <a-breadcrumb>
                            <a-breadcrumb-item
                                v-for="item in breadcrumbItem"
                                :key="item.key"
                            >
                                <!-- <a-icon type="profile" v-if="item.key == '0'" /> -->
                                <span
                                    v-html="item.title"
                                    @click="handleClickBreadcrumb(item)"
                                />
                            </a-breadcrumb-item>
                        </a-breadcrumb>
                    </a-layout-header>
                    <a-layout-content>
                        <div class="clearfix">
                            <ul class="fileList">
                                <li
                                    v-for="item in files"
                                    :key="item.index"
                                    :draggable="item.children ? false : true"
                                    @dblclick="folderClick(item)"
                                    @dragstart="dragenter(item)"
                                    id="dragtarget"
                                >
                                    <a-icon
                                        theme="twoTone"
                                        :type="
                                            item.title.split('.').pop() ===
                                            'png'
                                                ? 'file-image'
                                                : item.title
                                                      .split('.')
                                                      .pop() === 'doc'
                                                ? 'file-word'
                                                : item.title
                                                      .split('.')
                                                      .pop() === 'xlsx'
                                                ? 'file-excel'
                                                : item.title
                                                      .split('.')
                                                      .pop() === 'pdf'
                                                ? 'file-pdf'
                                                : 'folder'
                                        "
                                    ></a-icon>
                                    <span :title="item.title">{{
                                        item.title
                                    }}</span>
                                </li>
                            </ul>
                        </div>
                    </a-layout-content>
                </a-layout>
            </a-layout>
        </a-spin>
    </div>
</template>
<script>
import XEUtils from 'xe-utils';
import { every, indexOf } from 'xe-utils/methods';
import axios from 'axios';
import Sortable from 'sortablejs';

export default {
    name: 'fileListPage',
    props: {
        data: Array,
        fileList: Array,
        topPath: String,
    },
    watch: {
        data: {
            //
            handler(val) {
                val = val || [];
                this.menuList = val;
            },
            immediate: true,
            deep: true,
        },
        fileList: {
            //
            handler(val) {
                val = val || [];
                this.files = val.map((item) => ({
                    children: item.children,
                    key: item.key,
                    title: item.title,
                    scopedSlots: { icon: 'custom' },
                    type: item.type,
                }));
                if (this.files && this.files.length) {
                    this.autoOpen();
                }
            },
            immediate: true,
            deep: true,
        },
    },
    data() {
        return {
            spinning: false,
            collapsed: false,
            menuList: [],
            treeArr: [],
            breadcrumbItem: [{ title: '', key: '0', path: '/index' }],
            selectedKeys: [],
            files: [],
            moveItem: {},
            draggable: true,
            open: {},
            expandedKeys: [],
        };
    },
    methods: {
        /**/
        handleSelectMenuItem(e) {
            let targetTitle = e.target.innerText;
            let menuTreeArr = XEUtils.toTreeArray(this.menuList);
            let target =
                menuTreeArr.filter((item) => item.title === targetTitle) || [];
            if (target && target.length) {
                this.selectedKeys = [];
                this.selectedKeys.push(target[0].key);
            }
        },
        /*header */
        handleClickBreadcrumb(item) {
            this.breadcrumbItem.map((ele, index) => {
                if (ele.key == item.key) {
                    this.breadcrumbItem.splice(
                        index + 1,
                        this.breadcrumbItem.length - (index + 1)
                    );
                }
            });
            if (item.key === '0') {
                this.files = this.fileList;
            }
        },
        /**/
        addNewItem(event) {
            console.log('');
            if (!this.moveItem.key) return;
            /**/
            let targetTitle = event.node.title || '';
            let targetKey = event.node.eventKey || '';

            /*  1.XXXXXXXXX
                3. -  endFlolder:'0'
                4. endFlolder:'1'
            */
            let menuTreeArr = XEUtils.toTreeArray(this.menuList);
            let targetFolder =
                XEUtils.searchTree(
                    this.menuList,
                    (item) => item.key === targetKey
                ) || []; /*tree*/
            let targetFolderTreeArr =
                XEUtils.toTreeArray(targetFolder) ||
                []; /*tree*/

            if (
                targetFolder &&
                targetFolder.length &&
                targetFolderTreeArr &&
                targetFolderTreeArr.length
            ) {
                //extraOptions.nodeType  'FILE'- 
                let target =
                    targetFolderTreeArr.filter(
                        (item) =>
                            item.key === targetKey && item.title === targetTitle
                    )[0] || {}; /**/
                let nodeType = target.extraOptions
                    ? target.extraOptions.nodeType
                    : target.type;
                if (
                    target.endFlolder === '0' &&
                    target.type != 'FILE' &&
                    target.type != 'AUDITEVIDENCE'
                )
                    return this.$message.error(' !');
                //workpaperId,
                if (
                    !target.endFlolder &&
                    target.type === 'FILE' &&
                    !target.extraOptions.workpaperId
                )
                    return this.$message.error(' !');
                /**/
                if (nodeType != 'FILE' && target.endFlolder === '1') {
                    let that = this;
                    if (
                        target.children &&
                        target.children.length &&
                        target.children.some(
                            (item) => item.title === this.moveItem.title
                        )
                    ) {
                        this.$confirm({
                            title: `${this.moveItem.title}`,
                            onOk() {
                                that.$emit('transferFile', {
                                    target: target,
                                    moveItem: that.moveItem,
                                    value: 'cover',
                                });
                            },
                            onCancel() {
                                that.clearData();
                            },
                        });
                    } else {
                        this.$emit('transferFile', {
                            target: target,
                            moveItem: this.moveItem,
                        });
                    }
                } else if (
                    !target.endFlolder &&
                    nodeType === 'FILE' &&
                    target.extraOptions.workpaperId
                ) {
                    /*  - 1. 2.*/
                    this.$emit('transferFile', {
                        target: target,
                        moveItem: this.moveItem,
                    });
                } else if (
                    target.endFlolder === '0' &&
                    target.type == 'AUDITEVIDENCE'
                ) {
                    // - 
                    this.$emit('transferFile', {
                        target: target,
                        moveItem: this.moveItem,
                        value: 'AUDITEVIDENCE',
                    });
                }
            }
        },
        clearData() {
            this.moveItem = {};
            this.active = false;
        },
        leave() {
            this.clearData();
        },
        folderClick(item) {
            //
            if (item.type === 'FILE') return;
            if (item.children && item.children.length) {
                let breadcrumbItemCache = this.breadcrumbItem.filter(
                    (item) => item.key == '0'
                );
                this.breadcrumbItem = [];
                this.breadcrumbItem.unshift(breadcrumbItemCache[0]);
                this.breadcrumbItem.push(item);
                this.files = item.children;
            }
        },
        dragenter(item) {
            if (item.type === 'FILE') {
                this.moveItem = item;
            }
        },
        arrive(e) {
            e.node.$el.style.backgroundColor = '#efeeec';
        },
        fileLeave(e) {
            e.node.$el.style.backgroundColor = '';
        },
        // 
        autoOpen() {
            let query = location.search;
            console.log('queryquery', query);
            if (query.indexOf('param') > -1) {
                const position = [];
                this.open = JSON.parse(`${decodeURI(query.split('=', 2)[1])}`);
                console.log('this.open', this.open);
                let str = this.open.index;
                var pos = str.indexOf('-');
                while (pos > -1) {
                    position.push(pos);
                    pos = str.indexOf('-', pos + 1);
                }
                str = str.replace(/\-/g, '.');
                for (var i = 0; i < position.length; i++) {
                    this.expandedKeys.push(str.slice(0, position[i]));
                }
                this.expandedKeys.push(str);
                // console.log('expandedKeys', this.expandedKeys);

                const tree = XEUtils.searchTree(
                    this.files,
                    (item) => item.title == this.open.userSubjectName
                );

                this.breadcrumbItem.push(tree);
            }
        },
    },
    beforeMount() {},
};
</script>
<style lang="scss" scoped>
.file-list-page {
    height: 100%;
    .ant-layout-header {
        width: 100%;
        -webkit-box-shadow: 0 0 5px 2px #dcdfe6;
        box-shadow: 0 0 5px 2px #dcdfe6;
        height: auto !important;
        min-height: 30px !important;
        background: #fff !important;
        line-height: 30px !important;
        padding: 0 !important;
    }
    .ant-layout-sider {
        background-color: white !important;
        border-right: 1px solid #dcdcdc;
        height: 100%;
        overflow: auto;
        width: 300px !important;
        max-width: 300px !important;
        flex: 0 0 300px !important;

        &::-webkit-scrollbar {
            width: 5px;
            height: 5px;
            background-color: #f5f5f5;
        }

        &::-webkit-scrollbar-thumb {
            background: #d8d8d8;
            border-radius: 5px;
        }

        .logo {
            display: flex;
            align-items: center;
            height: 60px;
            border-bottom: 1px solid #dcdcdc;

            img {
                height: 100%;
            }
            span {
                font-size: 22px;
                color: #409eff;
            }
        }
    }
    .ant-tree {
        li {
            cursor: pointer;
            .ant-tree-title:hover {
                color: white !important;
            }
            span[draggable],
            span[draggable='true'] {
                border-top: 0 !important;
                border-bottom: 0 !important;
            }
            span {
                font-size: 16px;
                line-height: 24px !important;
                color: gray !important;
            }

            .ant-tree-node-content-wrapper::before {
                padding-top: 20px !important;
                background-color: unset !important;
            }
        }

        .ant-tree-directory {
            & > li {
                span {
                    .ant-tree-node-content-wrapper {
                        .ant-tree-node-selected {
                            .ant-tree-title {
                                padding: 3px !important;
                            }
                        }
                        &:hover::before {
                            background-color: #ecebeb !important;
                            /* color: white !important; */
                        }
                    }
                }
            }
        }
    }
    /**/
    .ant-layout-header {
        border-bottom: 1px solid #dcdcdc;
        .ant-breadcrumb {
            margin-left: 8px;
            height: 100%;
            font-size: 16px;
            margin: 5px 0;
            padding-left: 20px;
            .ant-breadcrumb-link {
                span {
                    line-height: 30px;
                    cursor: pointer;
                }
            }
        }
    }
    /**/
    .ant-layout-content {
        background: #fff !important;
        .clearfix {
            ul {
                display: flex;
                flex-direction: column;
                margin-left: 40px;

                li {
                    display: flex;
                    flex-direction: row;
                    justify-content: flex-start;
                    align-items: center;
                    border-bottom: 1px solid #dcdcdc;
                    padding-left: 20px;

                    cursor: pointer;
                    &:hover {
                        // border: 3px solid #1ed0ff;
                        background-color: #efeeec;
                    }
                    .anticon {
                        font-size: 55px;
                        margin: 8px 0;
                    }
                    span {
                        // overflow-wrap: anywhere;
                        overflow: hidden;
                        word-break: keep-all;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        padding: 0 5px;
                    }
                }
            }
        }
    }
}
</style>


